// Variables

// Colors
$colors: (
  'white': #ffffff,
  'offwhite': #fff,
  'black': #34302d,
  'dark': #141414,
  'highlight': #597F57
);

@function color($color-name) {
  @return map-get($colors, $color-name);
}

// Breakpoints
$breakpoint-sm: 520px;
$breakpoint-md: 768px;
$breakpoint-lg: 1000px;
$breakpoint-nav: 1100px;
$breakpoint-xl: 1200px;

// Site Wrapper
$sitewrapper: 1400px;

// Padding/spacing
$beat: 10px;
$padding-sm: $beat * 2;
$padding-md: $beat * 4;
$padding-lg: $beat * 6;